<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="8">
        <v-card>
          <div class="d-flex">
            <div>
              <v-card-title>Edit Profile</v-card-title>
              <v-card-subtitle
                ><small
                  >Note: All required fields is marked with (*)</small
                ></v-card-subtitle
              >
            </div>
          </div>
          <v-form ref="form" disabled>
            <v-card-text>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    ref="first_name"
                    v-model="form.first_name"
                    label="First name*"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    ref="middle_name"
                    v-model="form.middle_name"
                    label="Middle name"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    ref="last_name"
                    v-model="form.last_name"
                    label="Last name*"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    ref="email"
                    v-model="form.email"
                    label="Email*"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    ref="employee_number"
                    v-model="form.employee_number"
                    label="Employee number*"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions class="pa-4">
              <v-btn color="primary" class="text-capitalize" disabled> Update </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>

      <v-col cols="12" md="4">
        <v-card disabled>
          <v-card-title>Change Password</v-card-title>
          <v-card-subtitle>
            <span>&nbsp;</span>
          </v-card-subtitle>
          <v-form ref="changePassForm" :disabled="loading">
            <v-card-text>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    ref="old_password"
                    v-model="changePassForm.old_password"
                    label="Old Password*"
                    :rules="[rules.required, validPassword]"
                    :append-icon="showOldPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="showOldPassword ? 'text' : 'password'"
                    @click:append="showOldPassword = !showOldPassword"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    ref="new_password"
                    v-model="changePassForm.new_password"
                    label="New Password*"
                    :rules="[
                      rules.required,
                      rules.minimum(6),
                      rules.maximum(100),
                      rules.strongPassword,
                      validNewPassword,
                    ]"
                    :append-icon="showNewPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="showNewPassword ? 'text' : 'password'"
                    @click:append="showNewPassword = !showNewPassword"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions class="pa-4">
              <v-btn
                color="primary"
                block
                @click="confirmationDialog = true"
                :disabled="loading"
                class="text-capitalize"
              >
                Update
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
    </v-row>

    <confirmation-message
      v-if="confirmationDialog"
      :confirmationData="confirmationData"
      @closeConfirmationDialog="closeConfirmationDialog"
      @onSubmit="onSubmit"
    />
  </v-container>
</template>

<script>
import formRules from "@/helpers/formRules";
import { mapGetters } from "vuex";
import ConfirmationMessage from "@/components/ConfirmationMessage.vue";
import { isObjectEmpty } from "@/helpers/form";

export default {
  components: { ConfirmationMessage },

  data: () => ({
    rules: formRules,
    form: {
      first_name: null,
      middle_name: null,
      last_name: null,
      email: null,
      employee_number: null,
    },
    changePassForm: {
      old_password: null,
      new_password: null,
    },
    confirmationDialog: false,
    confirmationData: {
      title: "Are you sure you want to update?",
      text: "This action cannot be undone.",
      closeBtnText: "Cancel",
      closeBtnColor: "gray",
      saveBtnText: "Update",
      saveBtnColor: "primary",
    },
    invalidPasswordText: null,
    invalidNewPasswordText: null,
    showOldPassword: false,
    showNewPassword: false,
  }),

  created() {
    this.getRecord();
  },

  computed: {
    ...mapGetters("auth", ["user", "loading", "errors"]),
  },

  methods: {
    getRecord() {
      this.form.first_name = this.user.first_name;
      this.form.middle_name = this.user.middle_name;
      this.form.last_name = this.user.last_name;
      this.form.email = this.user.email;
      this.form.employee_number = this.user.employee_number;
    },

    validPassword(val) {
      if (val == this.invalidPasswordText) {
        return "Incorrect password.";
      } else {
        return true;
      }
    },

    validNewPassword(val) {
      if (val == this.invalidNewPasswordText) {
        return "New password must be different.";
      } else {
        return true;
      }
    },

    async onSubmit() {
      this.confirmationDialog = false;
      if (this.$refs.changePassForm.validate()) {
        await this.$store.dispatch("auth/changePassword", this.changePassForm);

        if (!isObjectEmpty(this.errors)) {
          let errorFocus = "";

          Object.entries(this.errors).forEach(([key, value]) => {
            if (value) {
              if (!errorFocus) {
                errorFocus = key;
              }

              if (key == "old_password") {
                this.invalidPasswordText = this.changePassForm.old_password;
              }

              if (key == "new_password") {
                this.invalidNewPasswordText = this.changePassForm.new_password;
              }

              this.$refs.changePassForm.validate();
              this.$refs[errorFocus].focus();
            }
          });
        } else {
          this.$refs.changePassForm.reset();
        }
      }
    },

    closeConfirmationDialog() {
      this.confirmationDialog = false;
    },
  },
};
</script>